import { useEffect, useState } from "react";
import { Box, Button, Card, CardActions, CardContent, CardHeader, Typography } from '@material-ui/core';
import CancelIcon from '@material-ui/icons/Cancel';
import { makeStyles } from '@material-ui/core/styles';
import { byTitle } from "helpers/inventory";
import { selectByTitle, selectCart } from "store/selectors";
import { useStore } from "store";
import { ADD_TO_CART, REMOVE_FROM_CART, REMOVE_FROM_LIST } from "store/actions";

const useStyles = makeStyles({
  card: {
    background: "#CCC",
    margin: "5px",
  },
  cardContent: {
    padding: "0",
  },
  cardHeader: {
    padding: "8px 16px",
  },
  inCart: {
    fontWeight: "bold",
    color: "rgb(15, 67, 140)",
  }
});

const Product = ({ title, butts }) => {
  const { state, dispatch } = useStore();
  const beers = selectByTitle(state, title);
  const cart = selectCart(state);
  const classes = useStyles();

  const [count, setCount] = useState(0);

  useEffect(() => {
    if (cart) {
      setCount(cart.filter(x => x === title).length);
    }
  }, [title, cart]);

  const handleAdd = () => {
    dispatch({type: ADD_TO_CART, payload: title});
    setCount(count + 1);
  };

  const handleRemove = () => {
    dispatch({type: REMOVE_FROM_CART, payload: title});
    if (count > 0) {
      setCount(count -1);
    }
  };

  const handleRemoveProduct = e => {
    dispatch({type: REMOVE_FROM_LIST, payload: title});
    handleRemove();
  };

  const inCart = beer => {
    const isInCart = cart.some(title => {
      return byTitle(title).some(b => b["beer"] === beer);
    });

    if (isInCart) {
      return "textSecondary";
    }

    return "textPrimary";
  };

  return (
    <Card className={classes.card}>
      <CardHeader title={title} className={classes.cardHeader} titleTypographyProps={{variant: "h6"}} />
      <CardContent className={classes.cardContent}>
        <Box
          display="flex"
          flexWrap="wrap"
          alignContent="flex-start"
          p={0}
          m={1}
        >
          { beers.map(beer => {
            return (
            <Box m={1}>
                <Typography classes={{colorTextSecondary: classes.inCart}} color={inCart(beer.beer)}>{`${beer["quantity"]}x ${beer["beer"]}`}</Typography>
            </Box>
            );
          }) }
        </Box>
      </CardContent>
      <CardActions disableSpacing={false}>
        <Button variant="outlined" onClick={handleAdd}>Add</Button>
        <Button variant="outlined" onClick={handleRemove}>Remove</Button>
        <Button>{count}</Button>
        <Button onClick={handleRemoveProduct}><CancelIcon /></Button>
      </CardActions>
    </Card>
  );
};

export default Product;
