import { Divider, List, ListItem, ListItemText, Paper, Typography } from '@material-ui/core';
import { makeStyles } from "@material-ui/core/styles";
import { useStore } from "store";
import { selectByTitle, selectCart, selectUniqueBeers } from "store/selectors";
import CartItem from "./CartItem";

const useStyles = makeStyles({
  divider: {
    margin: "8px",
  },
});


const beersWithCounts = (state, allBeers, cartDatas) => {
  const newList = {};
  const zeroList = {};

  allBeers.forEach(beer => {
    zeroList[beer] = 0;
  });

  cartDatas.forEach(title => {
    const beerByTitle = selectByTitle(state, title);
    beerByTitle.forEach(bbt => {
      const beer = bbt["beer"];
      delete zeroList[bbt["beer"]];
      if (!(beer in newList)) {
        newList[beer] = 0;
      }
      newList[bbt["beer"]] += parseInt(bbt["quantity"]);
    });
  });

  const sortedNewList = {};
  Object.keys(newList).sort().forEach(key => {
    sortedNewList[key] = newList[key];
  });

  const sortedZeroList = {};
  Object.keys(zeroList).sort().forEach(key => {
    sortedZeroList[key] = zeroList[key];
  });

  return {...sortedNewList, ...sortedZeroList};
};

const Cart = () => {
  const classes = useStyles();
  const { state } = useStore();
  const reducer = (accumulator, currentValue) => accumulator + currentValue;
  const cartDatas = selectCart(state);
  const allBeers = selectUniqueBeers(state);
  const beerList = beersWithCounts(state, allBeers, cartDatas);

  return (
    <Paper elevation={3}>
      <Typography variant="h4">Cart</Typography>
      <Typography variant="h5">What to Buy</Typography>

      <List>
        {cartDatas.sort().map(value => {
          return <ListItem key={`c-${value}`}><ListItemText primary={value} /></ListItem>;
        })}
      </List>

      <Divider className={classes.divider} />

      <Typography variant="h5">
        Beer Totals ( {Object.values(beerList).reduce(reducer, 0)} cans )
      </Typography>

      <List>
        {Object.keys(beerList).map(value => {
          return <CartItem key={`ci-${value}`} beer={value} quantity={beerList[value]} />;
        })}
      </List>
    </Paper>
  );
};

export default Cart;
