import { Divider, Grid, Typography } from '@material-ui/core';
import { byType } from "./helpers/inventory";
import { useStore } from "store";
import { selectByType, selectCustomList, selectUniqueTitles } from "store/selectors";
import Cart from "./components/Cart";
import ProductList from "./components/ProductList";
import './App.css';

function App() {
  const { state } = useStore();
  const mainList = selectCustomList(state);
  const uniqueTitles = selectUniqueTitles(state, {type: "mixed"});

  return (
    <Grid container>
      <Grid item xs={9}>
        <ProductList items={uniqueTitles} />

        <Divider />

        <Typography variant="h3">Cases</Typography>
        <ProductList items={selectByType(state, "case").map(beer => beer["title"])} />

        <Divider />

        <Typography variant="h3">4 Packs</Typography>
        <ProductList items={byType("four").map(beer => beer["title"])} />

        <Divider />

        <Typography variant="h3">6 Packs</Typography>
        <ProductList items={byType("six").map(beer => beer["title"])} />

        <Divider />

        <Typography variant="h3">12 Packs</Typography>
        <ProductList items={byType("twelve").map(beer => beer["title"])} />

        <Divider />

        <Typography variant="h3">Singles</Typography>
        <ProductList items={byType("single").map(beer => beer["title"])} />
      </Grid>
      <Grid item xs={3}>
        <Cart key={"cart"} />
      </Grid>
    </Grid>
  );
}

export default App;

